import React, {Component} from 'react';
import {
  Col,
  FormFeedback,
  FormGroup,
  Input,
  InputGroup,
  InputGroupAddon,
  Label,
  Row
} from 'reactstrap';
import Dropzone from 'react-dropzone';
import {LAYOUTS, PRODUCTS_MAP} from "../constants";
import classNames from "classnames"

export class One extends Component {

  constructor(props) {
    super(props);

    let value = this.props.getStore().name;
    this.state = {
      valid: value !== '' ? true : null,
      error: null,
      value: value
    };
  }

  validate() {
    if (this.state.value === '') {
      this.setState({valid: false, error: 'Please enter the name of your project.'});
    }
    this.props.updateStore({
      name: this.state.value
    });
    return this.state.valid;
  };

  handleChange = e => {
    let state = {
      value: e.target.value,
      valid: e.target.value !== '',
      error: ''
    };
    this.setState(state);
  };

  render() {
    return (
      <div className="StepOne">
        <FormGroup>
          <Input size="lg"
                 valid={this.state.valid}
                 placeholder="Insert name of your project"
                 defaultValue={this.state.value}
                 onChange={this.handleChange}/>
          <FormFeedback>{this.state.error}</FormFeedback>
        </FormGroup>
      </div>
    );
  }
}

export class Two extends Component {

  constructor(props) {
    super(props);
    let store = this.props.getStore();
    let value = store.product || '';
    this.state = {
      value: value,
      valid: value !== '' ? true : null,
    };
  }

  validate() {
    if (this.state.value === '') {
      this.setState({
        valid: false,
      });
    }
    return this.state.valid;
  };

  handleChange = e => {
    this.setState({
      valid: e.target.value !== '',
      value: e.target.value
    });
    this.props.updateStore({
      product: e.target.value
    });
  };

  // render the steps as stepsNavigation
  renderProducts(products) {
    return products.map((product) => (
      <td key={product.name}
          className={"StepTwo-product StepTwo-product-" + product.name.toLowerCase()}>
        <FormGroup className={this.state.value === product.name ? "checked" : null}>
          <Label>
            <div className="product-box"></div>
            <div className="product-name">{product.name}</div>
            <div className="product-desc">{product.size}</div>
            <div className="custom-control custom-radio">
              <Input type="radio"
                     className="custom-control-input"
                     value={product.name}
                     checked={this.state.value === product.name}
                     valid={this.state.valid}
                     onChange={this.handleChange}/>
              <span className="custom-control-indicator"></span>
            </div>
          </Label>
        </FormGroup>
      </td>
    ));
  }

  render() {
    return (
      <div className="StepTwo">
        <Row>
          <Col lg="6" xs="12">
            <h2>Magnetic Notes</h2>
            <table className="table">
              <tbody>
              <tr>
                {this.renderProducts([
                  PRODUCTS_MAP.S,
                  PRODUCTS_MAP.M,
                  PRODUCTS_MAP.L
                ])}
              </tr>
              </tbody>
            </table>
          </Col>
          <Col lg="6" xs="12">
            <h2>Magnetic Pad</h2>
            <table className="table">
              <tbody>
              <tr>
                {this.renderProducts([
                  PRODUCTS_MAP.A5,
                  PRODUCTS_MAP.A4,
                  PRODUCTS_MAP.A3
                ])}
              </tr>
              </tbody>
            </table>
          </Col>
        </Row>
      </div>
    );
  }
}

export class Three extends Component {

  constructor(props) {
    super(props);
    let store = this.props.getStore();
    this.state = {
      frontInner: store.frontInner,
      frontOuter: store.frontOuter,
      backInner: store.backInner,
      backOuter: store.backOuter,
    };
  }

  handleSelect = (type, file) => {
    let state = {};
    state[type] = file;
    this.setState(state);
    this.props.updateStore(state);
  };

  handleDelete = (type) => {
    let state = {};
    state[type] = null;
    this.setState(state);
    this.props.updateStore(state);
  };

  renderSections(items) {
    return Object.keys(items).map((i) => (
      <div key={i} className="item">
        <div className="item-heading">{items[i].title}</div>
        <div className="item-body">
          {this.state[i] ? (
            <div className="item-file">
              {this.state[i].name}
              <button className="btn btn-link"
                      onClick={() => {
                        this.handleDelete(i)
                      }}>Delete
              </button>
            </div>
          ) : (
            <div className="item-select-file">
              <Dropzone
                className="btn btn-lg btn-outline-secondary"
                acceptClassName="btn-outline-success"
                rejectClassName="btn-outline-danger"
                multiple={false}
                accept={this.props.accept}
                onDrop={(files) => {
                  this.handleSelect(i, files[0])
                }}
              >
                Select layout
              </Dropzone>
              <button className="btn btn-lg btn-outline-secondary">Leave empty</button>
            </div>
          )}

        </div>
      </div>
    ));
  }

  render() {
    return (
      <div className="StepThree">
        <Row>
          <Col lg="6" xs="12">
            <p>
              You mockups must be strictly in PDF format.
              Please, make sure that your files comply with all the
              {" "}
              <a target="_blank" rel="noopener noreferrer" href="https://teslaamazing.com">
                technical requirements
              </a>
            </p>
            <hr/>
            {this.renderSections(this.props.sections)}
          </Col>
          <Col lg="6" xs="12">
            <div className="jumbotron">
              ...
            </div>
          </Col>
        </Row>
      </div>
    );
  }
}

Three.defaultProps = {
  accept: 'application/pdf',
  sections: LAYOUTS
};

export class Four extends Component {

  constructor(props) {
    super(props);
    this.store = this.props.getStore();
  }

  renderCatalog(catalog) {
    return catalog.map((item, key) => (
      <div key={key} className="card">
        <div className="card-header">
          {item.title}
        </div>
        <div className="card-body">
          <div className="products">
            <Row>
              {item.items.map((product, key) => (
                <Col key={key} xs="6">
                  <InputColor color={product.color} input={{
                    defaultValue: this.store.items[product.sku] !== undefined ? this.store.items[product.sku] : "",
                    onChange: this.handleChange,
                    name: product.sku,
                  }}/>
                </Col>
              ))}
            </Row>
          </div>
        </div>
      </div>
    ));

  }

  render() {
    let catalog = PRODUCTS_MAP[this.store.product] !== undefined ? PRODUCTS_MAP[this.store.product].catalog : [];
    return (
      <div className="StepFour">
        <Row>
          <Col lg="5" xs="12">
            {this.renderCatalog(catalog)}
          </Col>
          <Col lg="7" xs="12">
            <div className="jumbotron">
              ...
            </div>
          </Col>
        </Row>
      </div>
    );
  }

  handleChange = e => {
    this.store.items[e.target.name] = e.target.value;
    this.props.updateStore({
      items: this.store.items
    });
  };

}

export class InputColor extends Component {
  render() {
    let _style = {};
    let _groupClasses = {};
    let _addonClasses = {};
    if (this.props.color.substr(0, 1) === "#") {
      _addonClasses["border-0"] = true;
      _style["backgroundColor"] = this.props.color;
    } else {
      _groupClasses["color-" + this.props.color] = true;
    }
    return (
      <InputGroup className={classNames(_groupClasses)}>
        <InputGroupAddon
          className={classNames(_addonClasses)}
          style={_style}>{this.props.color === "transparent" && (
          <span>transparent</span>)}</InputGroupAddon>
        <Input {...this.props.input}/>
      </InputGroup>
    );
  }
}

InputColor.defaultProps = {
  color: null
};




